@import "../../../node_modules/bootstrap/scss/bootstrap-reboot.scss";
@import "../../../node_modules/bootstrap/scss/utilities";
@import "../../../node_modules/bootstrap/scss/grid";
@import "../../../node_modules/bootstrap/scss/buttons";
@import "../../../node_modules/bootstrap/scss/forms";
@import "../../../node_modules/bootstrap/scss/custom-forms.scss";
@import "../../../node_modules/bootstrap/scss/tables.scss";
@import url("../fonts/stylesheet.css");

body {
  margin-top: 30px;
  font-family: TT Norms;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  color: #000000;
}

h1 {
  font-size: 20px;
}

h2 {
  font-size: 16px;
}

.header {
  margin-bottom: 20px;
  .header_search-form {
    font-size: 16px;
    line-height: 24px;
    color: #8c8c8c;
  }
}

.unitData {
  margin-bottom: 46px;
}

.unitInfo {
  align-items: center;
  margin-bottom: 30px;
  font-size: 20px;
  line-height: 24px;
}

.dateFilter {
  margin-bottom: 20px;
}

.custom-radio {
  margin-bottom: 10px;
}

.btnReport {
  margin-top: 20px;
}

.deviceInfo {
  height: 330px;
  border-radius: 5px;
  background-color: #f7f7f7;
  padding: 10px;
  font-size: 14px;
  line-height: 17px;
  span {
    color: #8c8c8c;
  }
  div {
    margin: 6px 0;
  }
  p {
    margin: 0;
    display: inline-block;
  }
  .IDs {
    color: #4990e2;
  }
}

.tableData {
  margin-left: 20px;
  border: 1px dashed #939da6;
  box-sizing: border-box;
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.15);
  padding: 20px;
  .tableHeader {
    font-size: 16px;
  }
  .intro {
    margin: 0 auto;
  }
  .thead {
    border: none;
  }
  .solutions {
    min-width: 220px;
  }
  th {
    font-weight: 400;
    font-size: 13px;
    line-height: 15px;
    color: #939da6;
  }
  td {
    font-weight: 400;
    font-size: 13px;
    line-height: 19px;
    color: #353535;
  }
  .checkIcon {
    margin-left: 20px;
  }
}
